<template>
  <!-- /results -->
  <!-- html and js autoinjects to App.vue (and therefore on public/index.html) -->
  <v-container>
    <Header />
    <Timedate style="margin-top:0" />
    <div class="container-transparent">
      <div class="section">
        <div class="col s12 center-align">
          <h1>Tulokset</h1>
        </div>
      </div>
      <v-row>
        <v-col md="4" offset-md="4" class="inputarea">
          <v-switch
            v-model="updateSwitch"
            class="black--text"
            color="orange darken-3"
            label="Tulosten automaattinen pivitys 60s vlein"
          ></v-switch>
        </v-col>
      </v-row>
      <v-row>
        <v-col order="first" style="margin-top:20px">
          <router-link to="/weighting">
            <v-btn large rounded color="blue darken-4" class="white--text"
              ><i class="material-icons left">fitness_center</i>Punnitus</v-btn
            >
          </router-link>
        </v-col>
        <v-col style="margin-top:20px">
          <router-link to="/overview">
            <v-btn large rounded color="primary"
              ><i class="material-icons left">info</i>Kilpailun
              yleisnkym</v-btn
            >
          </router-link>
        </v-col>
        <!-- If one of these has results, show "Lataa kaikki pdf" button -->
        <v-col
          order="last"
          v-if="
            normal_points.length ||
              team_results.length ||
              biggest_fishes_results.length ||
              biggest_amounts_results.length
          "
          style="margin-top:20px"
        >
          <v-dialog v-model="dialog" scrollable max-width="300px">
            <template v-slot:activator="{ on, attrs }">
              <v-btn
                color="green darken-4"
                dark
                v-bind="attrs"
                v-on="on"
                large
                tile
                class="white--text"
                :loading="loading"
                :disabled="!biggest_amounts_results.length || !competition"
              >
                <i class="material-icons left">picture_as_pdf</i>Lataa kaikki
                tulokset
              </v-btn>
            </template>
            <v-card>
              <v-card-title>Valitse mit lataus sislt</v-card-title>
              <v-divider></v-divider>
              <v-card-text style="height: 300px;width:100%;">
                <v-checkbox
                  v-model="selected_print"
                  label="Tilastoja"
                  :disabled="!biggest_amounts_results.length"
                  color="indigo darken-3"
                  value="stats"
                ></v-checkbox>
                <v-checkbox
                  v-model="selected_print"
                  :disabled="!biggest_amounts_results.length"
                  label="Normaalikilpailun tulokset"
                  color="indigo darken-3"
                  value="normal"
                ></v-checkbox>
                <v-checkbox
                  v-if="isTeamCompetition"
                  :disabled="!team_results.length"
                  v-model="selected_print"
                  label="Tiimikilpailun tulokset"
                  color="indigo darken-3"
                  value="team"
                ></v-checkbox>
                <v-checkbox
                  v-model="selected_print"
                  :disabled="!biggest_fishes_results.length"
                  label="Suurimmat yksittiset kalat"
                  color="indigo darken-3"
                  value="biggest_fishes"
                ></v-checkbox>
                <v-checkbox
                  v-model="selected_print"
                  :disabled="!biggest_amounts_results.length"
                  label="Suurimmat kalasaaliit"
                  color="indigo darken-3"
                  value="biggest_amounts"
                ></v-checkbox>
                <v-checkbox
                  v-model="selected_print"
                  :disabled="
                    !biggest_amounts_results.length &&
                      !biggest_amounts_results.length
                  "
                  label="Suurimmat yksittiset kalat / kalasaaliit (Voittajat)"
                  color="indigo darken-3"
                  value="biggest_winners"
                ></v-checkbox>
              </v-card-text>
              <v-divider></v-divider>
              <v-card-actions>
                <v-col>
                  <v-btn
                    outlined
                    color="red darken-4"
                    text
                    @click="dialog = false"
                    >Sulje</v-btn
                  >
                </v-col>
                <v-col>
                  <v-btn
                    outlined
                    color="green darken-4"
                    text
                    @click="choosePrints"
                    >Lataa</v-btn
                  >
                </v-col>
              </v-card-actions>
            </v-card>
          </v-dialog>
        </v-col>
      </v-row>

      <!-- Tabs -->
      <v-tabs
        v-model="tab"
        background-color="blue lighten-2"
        color="basil"
        grow
        show-arrows
      >
        <v-tabs-slider color="blue darken-4"></v-tabs-slider>
        <v-tab href="#stats">Tilastoja</v-tab>
        <v-tab href="#normal-competition">Normaalikilpailu</v-tab>
        <v-tab v-if="isTeamCompetition" href="#team-competition"
          >Tiimikilpailu</v-tab
        >
        <v-tab v-else href="#team-competition" disabled
          >Ei Tiimikilpailua</v-tab
        >
        <v-tab href="#biggest-fishes">Suurimmat Kalat</v-tab>
        <v-tab href="#biggest-fish-amounts">Suurimmat Kalasaaliit</v-tab>
      </v-tabs>

      <v-tabs-items v-model="tab" style="background: rgba(0,0,0,0.4);">
        <!-- Tilastoja -->
        <v-tab-item class="inputarea" :value="'stats'">
          <!-- Save as pdf button, is disabled if there are no results -->
          <v-row style="padding-top:50px">
            <v-col md="3" offset-md="8">
              <v-btn
                large
                tile
                color="green darken-4"
                class="white--text"
                @click="saveStatsAsPDF(`Tilastoja`)"
                :disabled="!biggest_amounts_results.length"
                style="margin-bottom:20px"
                ><i class="material-icons left">picture_as_pdf</i>Lataa
                pdf</v-btn
              >
            </v-col>
          </v-row>
          <v-row style="padding-bottom:50px">
            <v-row>
              <v-col md="12">
                <div
                  class="chart-container"
                  style="position: relative;height:100%,width:100%;"
                >
                  <canvas id="fishesChart"></canvas>
                </div>
              </v-col>
              <v-col md="12">
                <div
                  class="chart-container"
                  style="position: relative;height:100%,width:100%;"
                >
                  <canvas id="signeesChart"></canvas>
                </div>
              </v-col>
            </v-row>
            <v-row>
              <v-col style="margin-top:50px">
                <v-divider class="black"></v-divider>
              </v-col>
            </v-row>
          </v-row>
          <v-row v-if="competition">
            <v-col md="8" offset-md="2">
              <v-row>
                <v-col>
                  <h3>Kalalajien mritykset</h3>
                </v-col>
              </v-row>
              <v-row>
                <v-col class="scroll_table">
                  <table
                    id="fish-weights-table"
                    class="striped highlight centered responsive-table table_header"
                  >
                    <thead>
                      <tr>
                        <th>Kalalaji</th>
                        <th>Pistekerroin</th>
                        <th>Alamitta</th>
                        <th>Punnittu</th>
                      </tr>
                    </thead>
                    <tbody>
                      <tr
                        v-for="(fish, index) in calculated_fish_weights"
                        :key="index"
                      >
                        <th style="border:1px solid black;" scope="row">
                          {{ fish.name }}
                        </th>
                        <td style="border:1px solid black;">
                          x {{ fish.multiplier }}
                        </td>
                        <td style="border:1px solid black;">
                          {{ fish.minsize }} cm
                        </td>
                        <td style="border:1px solid black;">
                          {{
                            fish.weights
                              ? Math.round(
                                  (fish.weights / 1000 + Number.EPSILON) * 100
                                ) / 100
                              : 0
                          }}
                          kg
                        </td>
                      </tr>
                      <tr>
                        <th scope="row"></th>
                        <th></th>
                        <th style="border:1px solid black;">
                          Saalista yhteens
                        </th>
                        <td style="border:1px solid black;">
                          <b
                            >{{
                              Math.round(
                                (calculated_total_weights / 1000 +
                                  Number.EPSILON) *
                                  100
                              ) / 100
                            }}
                            kg</b
                          >
                        </td>
                      </tr>
                    </tbody>
                  </table>
                </v-col>
              </v-row>

              <v-row>
                <v-col>
                  <h3>Yleisi tilastoja</h3>
                </v-col>
              </v-row>
              <v-row>
                <v-col md="10" offset-md="1">
                  <table
                    id="misc-table"
                    class="striped centered responsive-table highlight"
                  >
                    <tr>
                      <th
                        style="border:1px solid black;"
                        class="center-align blue_th"
                      >
                        <b>Cup pistekerroin</b>
                      </th>
                      <td style="border:1px solid black;" class="center-align">
                        <b>x {{ competition.cup_points_multiplier }}</b>
                      </td>
                    </tr>
                    <tr>
                      <th
                        style="border:1px solid black;"
                        class="center-align blue_th"
                      >
                        <b>Ilmoittautuneita yhteens</b>
                      </th>
                      <td style="border:1px solid black;" class="center-align">
                        <b>{{ signees.length }}</b> venett
                      </td>
                    </tr>
                    <tr>
                      <th
                        style="border:1px solid black;"
                        class="center-align blue_th"
                      >
                        <b>Saalista saaneita</b>
                      </th>
                      <td
                        style="border:1px solid black;"
                        class="center-align"
                        v-if="$store.getters.getPointSignees.length"
                      >
                        <b
                          >{{
                            Math.round(
                              ($store.getters.getPointSignees.length /
                                competition.signees.length) *
                                100 *
                                100
                            ) / 100
                          }}% ({{ $store.getters.getPointSignees.length }} /
                          {{ signees.length }})</b
                        >
                      </td>
                      <td
                        style="border:1px solid black;"
                        class="center-align"
                        v-else
                      >
                        <b>0% (0/0)</b>
                      </td>
                    </tr>
                  </table>
                </v-col>
              </v-row>
            </v-col>
          </v-row>
        </v-tab-item>

        <!-- Normaalikilpailu -->
        <v-tab-item class="inputarea" :value="'normal-competition'">
          <v-row>
            <v-col
              md="10"
              offset-md="1"
              style="padding-top:50px;padding-bottom:20px"
            >
              <v-row>
                <v-col md="4" offset-md="4">
                  <v-select
                    label="Valitse nytettvt tulokset"
                    outlined
                    :items="normal_options"
                    @input="switchNormalResults"
                    v-model="selected_normal"
                  />
                </v-col>
                <v-col v-if="normal_points.length" md="3" offset-md="1">
                  <v-btn
                    large
                    tile
                    color="green darken-4"
                    class="white--text"
                    @click="
                      saveAsPDF(
                        `Normaalikilpailun tulokset (${selected_normal})`,
                        '#normal-table'
                      )
                    "
                  >
                    <i class="material-icons left">picture_as_pdf</i>Lataa pdf
                  </v-btn>
                </v-col>
              </v-row>
              <v-row v-if="normal_points.length">
                <v-col md="12">
                  <p v-if="normal_points.length" class="flow-text">
                    Normaalikilpailu ({{ selected_normal }})
                  </p>
                </v-col>
              </v-row>
              <v-row class="row" v-if="normal_points.length">
                <v-col class="scroll_table" md="12">
                  <!--TODO Possibly change tables to https://vuetifyjs.com/en/components/data-tables/#data-tables ? or implement on vue-->
                  <table
                    id="normal-table"
                    class="highlight centered responsive-table tablearea table_header scroll_table"
                  >
                    <thead>
                      <tr>
                        <th
                          v-for="(header, index) in normal_headers"
                          :key="index"
                        >
                          {{ header }}
                        </th>
                      </tr>
                    </thead>
                    <tbody v-if="selected_normal == 'Pisteet'">
                      <tr v-for="(signee, index) in normal_points" :key="index">
                        <th class="center-align" style="border:1px solid black">
                          {{ signee.placement }}.
                        </th>
                        <td style="border:1px solid black">
                          ({{ signee.boat_number }})
                        </td>
                        <td style="border:1px solid black">
                          {{ signee.captain_name }}
                        </td>
                        <td style="border:1px solid black">
                          {{ signee.temp_captain_name }}
                        </td>
                        <td style="border:1px solid black">
                          {{ signee.locality }}
                        </td>
                        <td style="border:1px solid black">
                          {{ signee.total_points.toLocaleString() }} p
                        </td>
                        <td style="border:1px solid black">
                          {{ signee.cup_placement_points }}
                        </td>
                        <td style="border:1px solid black">
                          {{ signee.cup_participation_points }}
                        </td>
                        <td style="border:1px solid black">
                          {{ signee.cup_points_total }}
                        </td>
                      </tr>
                    </tbody>
                    <tbody v-else>
                      <tr
                        v-for="(signee, index) in normal_weights"
                        :key="index"
                      >
                        <th class="center-align" style="border:1px solid black">
                          {{ signee.placement }}.
                        </th>
                        <td style="border:1px solid black">
                          ({{ signee.boat_number }})
                        </td>
                        <td style="border:1px solid black">
                          {{ signee.captain_name }}
                        </td>
                        <td
                          v-for="(name, index) in table_fish_names"
                          :key="index"
                          style="border:1px solid black"
                        >
                          {{ signee[name].toLocaleString() }} g
                        </td>
                        <td style="border:1px solid black">
                          {{ signee.total_points.toLocaleString() }} p
                        </td>
                      </tr>
                    </tbody>
                  </table>
                </v-col>
              </v-row>
              <v-row v-else>
                <v-col v-if="!loading">
                  <p class="flow-text">Ei tuloksia, viel...</p>
                </v-col>
                <v-col v-else>
                  <h2>Pivitetn tuloksia tietokannasta...</h2>
                  <ProgressBarQuery />
                </v-col>
              </v-row>
            </v-col>
          </v-row>
        </v-tab-item>

        <!-- Tiimikilpailu -->
        <v-tab-item
          class="inputarea"
          :value="'team-competition'"
          v-if="isTeamCompetition"
        >
          <v-row v-if="team_results.length">
            <v-col style="padding-top:50px;" md="3" offset-md="8">
              <v-btn
                large
                tile
                color="green darken-4"
                class="white--text"
                @click="saveAsPDF(`Tiimikilpailun tulokset`, '#team-table')"
              >
                <i class="material-icons left">picture_as_pdf</i>Lataa pdf
              </v-btn>
            </v-col>
          </v-row>
          <v-row>
            <v-col
              md="10"
              offset-md="1"
              style="padding-bottom:20px"
              v-if="team_results.length"
              class="scroll_table"
            >
              <table
                id="team-table"
                class="highlight centered responsive-table tablearea"
              >
                <caption
                  v-if="team_results.length"
                  class="center-align flow-text"
                >
                  Tiimikilpailu
                </caption>
                <thead style="background: rgb(0, 1, 34);color:#fff;">
                  <tr>
                    <th>Sijoitus</th>
                    <th>Tiimi</th>
                    <th>Jsen 1</th>
                    <th>Jsen 2</th>
                    <th>Jsen 3</th>
                    <th>Pisteet</th>
                  </tr>
                </thead>
                <tbody>
                  <tr v-for="(team, index) in team_results" :key="index">
                    <th class="center-align" style="border:1px solid black">
                      {{ index + 1 }}.
                    </th>
                    <td style="border:1px solid black">{{ team.name }}</td>
                    <td style="border:1px solid black">
                      {{ team.captain_name_1 }}
                    </td>
                    <td style="border:1px solid black">
                      {{ team.captain_name_2 }}
                    </td>
                    <td style="border:1px solid black">
                      {{ team.captain_name_3 }}
                    </td>
                    <td style="border:1px solid black">
                      {{ team.points.toLocaleString() }} p
                    </td>
                  </tr>
                </tbody>
              </table>
            </v-col>
            <v-col v-else>
              <v-col v-if="!loading"> </v-col>
              <p v-if="!loading" class="flow-text">Ei tuloksia, viel...</p>
              <v-col v-else>
                <h2>Pivitetn tuloksia tietokannasta...</h2>
                <ProgressBarQuery />
              </v-col>
            </v-col>
          </v-row>
        </v-tab-item>

        <!-- Suurimmat kalat -->
        <v-tab-item class="inputarea" :value="'biggest-fishes'">
          <v-row style="padding-top:50px">
            <v-col md="3" offset-md="4">
              <v-select
                label="Valitse nytettvt tulokset"
                outlined
                @input="calculateBiggestFishes"
                v-model="selected_biggest_fish"
                :items="fish_names"
              />
            </v-col>
            <v-col md="3" offset-md="1" v-if="biggest_fishes_results.length">
              <v-btn
                large
                tile
                color="green darken-4"
                class="white--text"
                @click="
                  saveAsPDF(
                    `Suurimmat kalat (${selected_biggest_fish})`,
                    '#biggest-fishes-table'
                  )
                "
              >
                <i class="material-icons left">picture_as_pdf</i>Lataa pdf
              </v-btn>
            </v-col>
          </v-row>
          <v-row v-if="biggest_fishes_results.length">
            <v-col>
              <p v-if="results_found_fishes" class="flow-text">
                Suurimmat kalat ({{ selected_biggest_fish }}
                {{ results_found_fishes }})
              </p>
              <p v-else class="flow-text">
                Suurimmat kalat ({{ selected_biggest_fish }})
              </p>
            </v-col>
          </v-row>
          <v-row v-if="biggest_fishes_results.length">
            <v-col class="scroll_table" md="10" offset-md="1">
              <table
                id="biggest-fishes-table"
                class="highlight centered responsive-table tablearea"
                style="margin-bottom:40px"
              >
                <thead style="background: rgb(0, 1, 34);color:#fff;">
                  <tr>
                    <th v-if="selected_biggest_fish == 'Voittajat'">
                      Kalalaji
                    </th>
                    <th v-else>Sijoitus</th>
                    <th>Veneen nro</th>
                    <th>Kapteeni</th>
                    <th>Paino</th>
                  </tr>
                </thead>
                <tbody>
                  <tr
                    v-for="(fish, index) in biggest_fishes_results"
                    :key="index"
                  >
                    <th
                      v-if="selected_biggest_fish == 'Voittajat'"
                      class="center-align"
                      style="border:1px solid black"
                    >
                      {{ fish.name }}
                    </th>
                    <th
                      v-else
                      class="center-align"
                      style="border:1px solid black"
                    >
                      {{ index + 1 }}.
                    </th>
                    <td style="border:1px solid black">
                      ({{ fish.boat_number }})
                    </td>
                    <td style="border:1px solid black">
                      {{ fish.captain_name }}
                    </td>
                    <td style="border:1px solid black">{{ fish.weight }} g</td>
                  </tr>
                </tbody>
              </table>
            </v-col>
          </v-row>
          <v-row v-else>
            <v-col v-if="!loading">
              <p class="flow-text">Ei tuloksia, viel...</p>
            </v-col>
            <v-col v-else>
              <h2>Pivitetn tuloksia tietokannasta...</h2>
              <ProgressBarQuery />
            </v-col>
          </v-row>
        </v-tab-item>

        <!-- Suurimmat kalasaaliit -->
        <v-tab-item class="inputarea" :value="'biggest-fish-amounts'">
          <v-row style="padding-top:50px">
            <v-col md="3" offset-md="4">
              <v-select
                label="Valitse nytettvt tulokset"
                outlined
                @input="calculateBiggestAmounts"
                v-model="selected_biggest_amount"
                :items="fish_amount_names"
              />
            </v-col>
            <v-col md="3" offset-md="1" v-if="biggest_amounts_results.length">
              <v-btn
                large
                tile
                color="green darken-4"
                class="white--text"
                @click="
                  saveAsPDF(
                    `Suurimmat kalat (${selected_biggest_amount})`,
                    '#biggest-amounts-table'
                  )
                "
                :disabled="!biggest_amounts_results.length"
              >
                <i class="material-icons left">picture_as_pdf</i>Lataa pdf
              </v-btn>
            </v-col>
          </v-row>
          <v-row v-if="biggest_amounts_results.length"
            >>
            <v-col>
              <p v-if="results_found_amounts" class="flow-text">
                Suurimmat kalasaaliit ({{ selected_biggest_amount }}
                {{ results_found_amounts }})
              </p>
              <p v-else class="flow-text">
                Suurimmat kalasaaliit ({{ selected_biggest_amount }})
              </p>
            </v-col>
          </v-row>
          <v-row v-if="biggest_amounts_results.length">
            <v-col class="scroll_table" md="10" offset-md="1">
              <table
                id="biggest-amounts-table"
                class="highlight centered responsive-table tablearea"
                style="margin-bottom:40px"
              >
                <thead style="background: rgb(0, 1, 34);color:#fff;">
                  <tr>
                    <th v-if="selected_biggest_amount == 'Voittajat'">
                      Kalalaji
                    </th>
                    <th v-else>Sijoitus</th>
                    <th>Veneen nro</th>
                    <th>Kapteeni</th>
                    <th>Paino</th>
                  </tr>
                </thead>
                <tbody>
                  <tr
                    v-for="(result, index) in biggest_amounts_results"
                    :key="index"
                  >
                    <th
                      v-if="selected_biggest_amount == 'Voittajat'"
                      class="center-align"
                      style="border:1px solid black"
                    >
                      {{ result.name }}
                    </th>
                    <th
                      v-else
                      class="center-align"
                      style="border:1px solid black"
                    >
                      {{ index + 1 }}.
                    </th>
                    <td style="border:1px solid black">
                      ({{ result.boat_number }})
                    </td>
                    <td style="border:1px solid black">
                      {{ result.captain_name }}
                    </td>
                    <td style="border:1px solid black">
                      {{ result.weight }} g
                    </td>
                  </tr>
                </tbody>
              </table>
            </v-col>
          </v-row>
          <v-row v-else>
            <v-col v-if="!loading" md="8" offset-md="2">
              <p class="flow-text">Ei tuloksia, viel...</p>
            </v-col>
            <v-col v-else md="8" offset-md="2">
              <h2>Pivitetn tuloksia tietokannasta...</h2>
              <ProgressBarQuery />
            </v-col>
          </v-row>
        </v-tab-item>
      </v-tabs-items>
      <div v-if="competition">
        <v-row v-if="!loading">
          <v-col v-if="competition.isPublic" style="margin-top:20px">
            <v-btn
              large
              tile
              color="grey darken-4"
              @click="publishCompetition(false)"
              class="white--text"
            >
              <i class="material-icons left">unpublished</i>Aseta kilpailu
              salaiseksi
            </v-btn>
          </v-col>
          <v-col v-else style="margin-top:20px">
            <v-btn
              large
              tile
              color="green darken-4"
              @click="publishCompetition(true)"
              class="white--text"
            >
              <i class="material-icons left">published_with_changes</i>Aseta
              kilpailu julkiseksi
            </v-btn>
          </v-col>
          <v-col style="margin-top:20px">
            <v-btn
              id="updatebtn"
              large
              tile
              color="blue darken-4"
              @click="refreshCompetition(competition._id)"
              class="white--text"
            >
              <i class="material-icons left">update</i>Pivit tulokset
            </v-btn>
          </v-col>
          <v-col v-if="competition.isFinished" style="margin-top:20px">
            <v-btn large tile color="yellow" @click="endCompetition(false)">
              <i class="material-icons left">not_started</i>Aseta kilpailu
              keskeneriseksi
            </v-btn>
          </v-col>
          <v-col v-else style="margin-top:20px">
            <v-btn
              large
              tile
              color="green darken-3"
              @click="endCompetition(true)"
              class="white--text"
            >
              <i class="material-icons left">emoji_events</i>Aseta kilpailu
              pttyneeksi
            </v-btn>
          </v-col>
        </v-row>
        <v-row v-else>
          <h2>Pivitetn tuloksia tietokannasta...</h2>
          <ProgressBarQuery />
        </v-row>
      </div>
    </div>
  </v-container>
</template>
<script>
"use strict";
import Timedate from "../components/layout/Timedate";
import Header from "../components/layout/Header";
import M from "materialize-css";
import jsPDF from "jspdf";
import "jspdf-autotable";
import moment from "moment";
import CompetitionService from "../CompetitionService";
import ProgressBarQuery from "../components/layout/ProgressBarQuery";
import Chart from "chart.js";
import "chartjs-plugin-labels";

export default {
  name: "Results",
  components: {
    Timedate,
    Header,
    ProgressBarQuery,
  },
  data() {
    return {
      loading: false,
      tab: null,
      signees: [],
      fish_names: [],
      fish_amount_names: [],
      table_fish_names: [],
      results: [],
      normal_points: [],
      normal_weights: [],
      team_results: [],
      biggest_fishes: {},
      biggest_fishes_results: [],
      selected_biggest_fish: null,
      biggest_amounts: {},
      biggest_amounts_results: [],
      selected_biggest_amount: null,
      results_found_fishes: "",
      results_found_amounts: "",
      competition: null,
      isTeamCompetition: true,
      calculated_total_weights: null,
      calculated_fish_weights: null,
      selected_normal: null,
      fishes_chart: null,
      signees_chart: null,
      timer_refresh: null,
      interval: 60000,
      normal_options: ["Pisteet", "Kalat"],
      normal_headers: [
        "Sijoitus",
        "Nro.",
        "Kapteeni",
        "Varakapteeni",
        "Paikkakunta",
        "Tulos",
        "Cup sij. pisteet",
        "Cup osal. pisteet",
        "Yht.",
      ],
      selected_print: [],
      dialog: false,
      updateSwitch: false,
    };
  },
  watch: {
    updateSwitch(newValue) {
      //called whenever switch1 changes
      if (newValue) {
        const competition = JSON.parse(localStorage.getItem("competition"));
        const competition_id = competition["id"];
        this.timer_refresh = setInterval(
          () => this.refreshCompetition(competition_id),
          this.interval
        );
      } else {
        clearInterval(this.timer_refresh);
      }
    },
  },
  mounted() {
    //Init materialize elements
    M.AutoInit();
    /* eslint-disable no-unused-vars */
    this.checkLogin();
    if (localStorage.getItem("competition") != null) {
      const competition = JSON.parse(localStorage.getItem("competition"));
      const competition_id = competition["id"];
      //Update competition every minute
      this.refreshCompetition(competition_id);
    }

    // Focus on top of the page when changing pages
    location.href = "#";
    location.href = "#app";
  },
  beforeDestroy() {
    // Clear timer
    clearInterval(this.timer_refresh);
  },
  methods: {
    choosePrints: function() {
      this.dialog = false;
      this.saveAllAsPDF();
    },
    // Fetch competition from database, and update all the arrays
    async refreshCompetition(competition_id) {
      this.loading = true;
      this.fish_names = []; // Fish names, including "Voittajat"
      this.fish_amount_names = [];
      this.table_fish_names = []; // only fish names
      try {
        // Query competition with id
        let competitions = await CompetitionService.getCompetition(
          competition_id
        );
        // If competition found
        if (competitions.length) {
          // Pick first result (the array should only have one, since id's are unique)
          this.competition = competitions[0];
          // Update to vuex, Assing variables and arrays from vuex (see client/store/index.js)
          this.$store.commit("refreshCompetition", this.competition);
          this.isTeamCompetition = this.$store.getters.isTeamCompetition;
          this.signees = this.$store.getters.getResultSignees;
          this.selected_normal = "Pisteet";
          this.normal_points = this.competition.normal_points;
          this.normal_weights = this.competition.normal_weights;
          if (this.isTeamCompetition) {
            this.team_results = this.competition.team_results;
          }
          this.biggest_fishes = this.$store.getters.getBiggestFishes;
          this.biggest_amounts = this.$store.getters.getBiggestAmounts;
          this.calculated_total_weights = this.$store.getters.getCompetitionTotalWeights;
          this.calculated_fish_weights = this.competition.fishes;

          let temp_fish_names = this.$store.getters.getCompetitionFishes;
          this.fish_names.push("Voittajat");
          this.fish_amount_names.push("Voittajat");
          temp_fish_names.forEach((fish) => {
            this.fish_names.push(fish.name);
            this.fish_amount_names.push(fish.name);
            this.table_fish_names.push(fish.name);
          });
          this.calculateAll();
          if (this.fishes_chart && this.signees_chart) {
            this.fishes_chart.destroy();
            this.signees_chart.destroy();
          }
          this.$nextTick(() => this.drawCharts());
          M.toast({ html: "Tiedot ajantasalla!" });
        } else {
          this.signees = [];
          this.biggest_fishes = [];
          this.biggest_amounts = [];
          console.log("No competition found on database...");
        }
      } catch (err) {
        console.error(err.message);
      }
      this.loading = false;
    },
    async publishCompetition(isPublic) {
      this.competition.isPublic = isPublic;
      this.updateToDatabase(this.competition);
    },
    async endCompetition(isFinished) {
      this.competition.isFinished = isFinished;
      this.competition.isFinished
        ? (this.competition.state = "Pttynyt")
        : (this.competition.state = "Kesken");
      this.updateToDatabase(this.competition);
    },

    async updateToDatabase(competition) {
      try {
        this.$store.commit("refreshCompetition", this.competition);
        this.loading = true;
        await CompetitionService.updateCompetition(
          this.competition._id,
          this.competition
        );
      } catch (err) {
        console.error(err.message);
      }
      this.loading = false;
    },
    // Parse data, define charts, draw them
    drawCharts: function() {
      let temp_weights = [];
      let colors = [];

      // Get fish weights, and color from array for fishesChart
      this.calculated_fish_weights.forEach((fish) => {
        temp_weights.push(fish.weights);
        colors.push(fish.color);
      });

      // Get data for signeesChart (total signees, and signees who have more than 0 points)
      let signee_data = [];
      let point_signees = this.$store.getters.getPointSignees.length;
      let no_points_signees = this.signees.length - point_signees;
      signee_data.push(point_signees);
      signee_data.push(no_points_signees);

      // Define fishesChart
      const fishes_chart_data = {
        type: "doughnut",
        data: {
          labels: this.table_fish_names, // Fish names
          datasets: [
            {
              label: "Paino (g)",
              backgroundColor: colors, // Colors
              data: temp_weights, // Weights
            },
          ],
        },
        options: {
          maintainAspectRatio: true,
          responsive: true,
          plugins: {
            labels: {
              // convert grams to kilograms and add "kg" to end of the label
              render: function(args) {
                return (
                  ((args.value / (1000 + Number.EPSILON)) * 100) / 100 + " kg"
                );
              },
              // Other options
              fontSize: 14,
              fontStyle: "bold",
              // draw text shadows under labels, default is false
              textShadow: true,
              position: "border",
              textmargin: 1,
              // text shadow intensity, default is 6
              shadowBlur: 1,
              // text shadow X offset, default is 3
              shadowOffsetX: 2,
              // text shadow Y offset, default is 3
              shadowOffsetY: 2,
              // text shadow color, default is 'rgba(0,0,0,0.3)'
              shadowColor: "rgba(0,0,0,1)",
              fontColor: "white",
              fontFamily: '"Lucida Console", Monaco, monospace',
            },
          },
          title: {
            display: true,
            text: "Kaloja saatu yhteens",
            fontSize: 28,
          },
        },
      };

      // Define signeesChart
      const signee_chart_data = {
        type: "pie",
        data: {
          labels: ["Kyll", "Ei saalista"],
          datasets: [
            {
              label: "Lukumr (kpl)",
              backgroundColor: ["#7fbf7f", "#ff7f7f"], // Green and red
              data: signee_data, // Data
            },
          ],
        },
        options: {
          maintainAspectRatio: true,
          responsive: true,
          plugins: {
            labels: {
              render: "percentage",
              fontSize: 26,
              fontStyle: "bold",
              fontColor: "#000",
              fontFamily: '"Lucida Console", Monaco, monospace',
              precision: 2,
            },
          },
          title: {
            display: true,
            text: "Saalista saaneita",
            fontSize: 28,
          },
        },
      };
      /* eslint-disable no-unused-vars */
      // Draw the charts to canvas
      var fishes_ctx = document.getElementById("fishesChart").getContext("2d");
      let fishes_chart = new Chart(fishes_ctx, fishes_chart_data);
      var signees_ctx = document
        .getElementById("signeesChart")
        .getContext("2d");
      let signees_chart = new Chart(signees_ctx, signee_chart_data);
      /* eslint-disable no-unused-vars */

      this.fishes_chart = fishes_chart;
      this.signees_chart = signees_chart;
    },
    //Check if user is logged in has admin status, update values to vuex (Header.vue updates based on these values)
    checkLogin: function() {
      // If login token present --> user is logged in
      if (localStorage.getItem("jwt") != null) {
        this.$store.state.logged_in = true;
        let user = JSON.parse(localStorage.getItem("user"));
        // Check if user is admin
        //TODO safer way to check this than use localstorage?
        user.is_admin == true
          ? (this.$store.state.is_admin = true)
          : (this.$store.state.is_admin = false);
      } else {
        //Not logger in, so not admin either
        this.$store.state.logged_in = false;
        this.$store.state.is_admin = false;
      }
    },

    // "Wrapper" to calculate all the results at once
    async calculateAll() {
      this.calculateBiggestFishes();
      this.calculateBiggestAmounts();
    },

    // Switch table headers and columns based on this.selected_normal value (v-select)
    switchNormalResults: function() {
      // Prevent v-selectt having no value, would show error
      if (!this.selected_normal) {
        this.selected_normal = "Pisteet";
      }
      // If "Pisteet" selected in v-select, update headers and this.results (table data)
      if (this.selected_normal === "Pisteet") {
        this.normal_headers = [
          "Sijoitus",
          "Nro.",
          "Kapteeni",
          "Varakapteeni",
          "Paikkakunta",
          "Tulos",
          "Cup sij. pisteet",
          "Cup osal. pisteet",
          "Yht.",
        ];
      }
      // If "Kalat" selected in v-select, update headers and this.results (table data)
      else {
        this.normal_headers = ["Sijoitus", "Nro.", "Kapteeni"];
        // Get fish names and add them to headers
        this.table_fish_names.forEach((name) => {
          this.normal_headers.push(name);
        });
        this.normal_headers.push("Tulos");
      }
    },
    // Sorts the dictionary based on weights
    sortDict: function(fishes) {
      if (fishes) {
        let all_results = [];
        let temp_results = [];
        let placement = 1;
        this.fish_names.forEach((name) => {
          // If fish name is not "Voittajat"
          if (name !== "Voittajat") {
            // For every fish name, sort the array
            if (fishes[name]) {
              temp_results = fishes[name].sort(function compare(a, b) {
                return parseInt(b.weight) - parseInt(a.weight);
              });
              fishes[name] = temp_results.filter((result) => result.weight > 0);
              // Now it's sorted so first element is fine for "voittajat" table
              if (fishes[name].length) {
                all_results.push({
                  name: name,
                  boat_number: temp_results[0].boat_number,
                  captain_name: temp_results[0].captain_name,
                  weight: temp_results[0].weight.toLocaleString(),
                });
              }
            }
          }
        });

        return fishes, all_results;
      } else {
        return {}, [];
      }
    },
    // Calculate "Suurimmat Kalat"
    calculateBiggestFishes: function() {
      let fishes = this.biggest_fishes;
      this.results_found_fishes = null;

      // Check v-select value, don't allow it to go null because it shows error
      if (!this.selected_biggest_fish) {
        this.selected_biggest_fish = "Voittajat";
      }
      if (this.selected_biggest_fish === "Voittajat") {
        this.biggest_fishes,
          (this.biggest_fishes_results = this.sortDict(fishes));
      } else {
        // If v-select (this.selected_biggest_fish) not "Voittajat", get fish related results and sort them
        // based on the v-select fish name
        let fish_results = [];
        if (fishes[this.selected_biggest_fish]) {
          fish_results = fishes[this.selected_biggest_fish].sort(
            function compare(a, b) {
              return parseInt(b.weight) - parseInt(a.weight);
            }
          );
          this.results_found_amounts = "";
        } else {
          this.results_found_fishes = "- Ei tuloksia";
        }
        this.biggest_fishes_results = fish_results;
      }
    },
    // Calculate "Suurimmat kalasaaliit", works exactly like the calculateBiggestFishes
    //TODO make these 2 to one function
    calculateBiggestAmounts: function() {
      let fishes = this.biggest_amounts;
      this.results_found_amount = "";
      if (!this.selected_biggest_amount) {
        this.selected_biggest_amount = "Voittajat";
      }
      if (this.selected_biggest_amount === "Voittajat") {
        this.biggest_amounts,
          (this.biggest_amounts_results = this.sortDict(fishes));
      } else {
        let fish_results = [];
        if (fishes[this.selected_biggest_amount]) {
          fish_results = fishes[this.selected_biggest_amount].sort(
            function compare(a, b) {
              return parseInt(b.weight) - parseInt(a.weight);
            }
          );
          fish_results = fish_results.filter(
            (result) => parseInt(result.weight) > 0
          );
          this.results_found_amounts = "";
        } else {
          this.results_found_amount = "- Ei tuloksia";
        }

        this.biggest_amounts_results = fish_results;
      }
    },
    // For naming the pdf, replace certain characters
    replaceAll: function(string, search, replace) {
      return string.split(search).join(replace);
    },
    // Parses dictionary/json to array, for pdf autotables
    dictToArray: function(dict, type) {
      const temp_arr = Object.entries(dict);
      const arr = [];
      let placement = 1;
      temp_arr.forEach((element) => {
        let values = Object.values(element[1]);
        // Normaalikilpailu, pisteet
        if (type === 1) {
          values[0] = String(values[0]) + ".";
          values[1] = "(" + String(values[1]) + ")";
          values[5] = values[5].toLocaleString() + " p";
        }
        // Normaalikilpailu, kalat
        if (type === 2) {
          values[0] = String(values[0]) + ".";
          values[1] = "(" + String(values[1]) + ")";
          for (let i of range(3, values.length - 2)) {
            values[i] = values[i].toLocaleString() + " g";
          }
          values[values.length - 1] =
            values[values.length - 1].toLocaleString() + " p";
        }
        // Suurimmat kalat, suurimmat kalasaaliit
        if (type === 3) {
          let temp_bnumber = values[0];
          let temp_captain = values[1];
          let temp_points = values[2].toLocaleString() + " g";
          values[0] = String(placement) + ".";
          values[1] = "(" + String(temp_bnumber) + ")";
          values[2] = temp_captain;
          values[3] = temp_points;
        }
        //Voittajat
        if (type === 4) {
          values[1] = "(" + String(values[1]) + ")";
          values[3] = values[values.length - 1].toLocaleString() + " g";
        }
        //Tiimikilpailu
        if (type === 5) {
          values[0] = String(values[0]) + ".";
          values[values.length - 1] =
            values[values.length - 1].toLocaleString() + " p";
        }
        //Pisteet
        if (type === 6) {
          let temp_bnumber = values[0];
          let temp_captain = values[1];
          let temp_fish_name = values[2];
          let temp_weight = values[3];
          let temp_points = values[4];
          values[0] = String(placement) + ".";
          values[1] = "(" + String(temp_bnumber) + ")";
          values[2] = temp_captain;
          values[3] = temp_fish_name;
          values[4] = temp_weight.toLocaleString() + " g";
          values[5] = temp_points.toLocaleString() + " p";
        }
        placement++;
        arr.push(values);
      });
      return arr;
    },
    // Returns date in format dd/mm/yyyy as string
    formatDate: function(start_date) {
      start_date = moment(start_date);
      let formatted_date = `${start_date.date()}.${start_date.month() +
        1}.${start_date.year()}`;

      return formatted_date;
    },
    capitalize_words: function(str) {
      return str.replace(/\w\S*/g, function(txt) {
        return txt.charAt(0).toUpperCase() + txt.substr(1).toLowerCase();
      });
    },
    // Convert the charts and the tables to pdf
    saveAsPDF: function(competition_type, table_id) {
      // Format dates for easier reding
      let temp_start_date = this.formatDate(this.competition.start_date);
      let temp_end_date = this.formatDate(this.competition.end_date);

      // PDF creation
      let doc = new jsPDF();
      // Title
      const title = `${this.competition.name}`;
      const date =
        temp_start_date === temp_end_date
          ? String(temp_start_date)
          : `${temp_start_date} - ${temp_end_date}`;
      const time = `${date}, Klo. ${this.competition.start_time} - ${this.competition.end_time}`;
      doc.setFontSize(24);
      doc.text(10, 10, title, { align: "left" });
      doc.setFontSize(14);
      doc.text(10, 20, this.competition.cup_name, { align: "left" });
      doc.text(10, 30, time, { align: "left" });
      doc.line(0, 35, 400, 35);
      doc.setFontSize(20);

      // Table, based on given table_id, and table title based on competition_type
      doc.text(100, 50, competition_type, { align: "center" });
      doc.autoTable({
        html: table_id,
        styles: {
          overflow: "linebreak",
          cellWidth: "wrap",
          rowPageBreak: "avoid",
          halign: "justify",
          fontSize: "8",
          lineColor: "100",
          lineWidth: ".25",
        },
        columnStyles: { text: { cellwidth: "auto" } },
        theme: "striped",
        pageBreak: "auto",
        tableWidth: "auto",
        startY: 55,
        margin: { top: 20 },
      });

      // Save the pdf
      doc.save(
        `${moment(this.competition.start_date).year()}_${this.replaceAll(
          this.competition.name,
          " ",
          ""
        )}_${this.replaceAll(
          this.capitalize_words(competition_type),
          " ",
          ""
        )}.pdf`
      );
    },
    saveStatsAsPDF: function(competition_type) {
      // Format dates for easier reding
      let temp_start_date = this.formatDate(this.competition.start_date);
      let temp_end_date = this.formatDate(this.competition.end_date);

      let doc = new jsPDF();

      // Title
      const title = `${this.competition.name}`;
      const date =
        temp_start_date === temp_end_date
          ? String(temp_start_date)
          : `${temp_start_date} - ${temp_end_date}`;
      const time = `${date}, Klo. ${this.competition.start_time} - ${this.competition.end_time}`;
      doc.setFontSize(24);
      doc.text(10, 10, title, { align: "left" });
      doc.setFontSize(14);
      doc.text(10, 20, this.competition.cup_name, { align: "left" });
      doc.text(10, 30, time, { align: "left" });
      doc.line(0, 35, 400, 35);
      doc.setFontSize(18);

      // "Tilastot"
      var fishesImg = document
        .getElementById("fishesChart")
        .toDataURL("image/png", 1.0);
      var signeeImg = document
        .getElementById("signeesChart")
        .toDataURL("image/png", 1.0);
      doc.addImage(fishesImg, "PNG", -30, 40, 180, 90);
      doc.addImage(signeeImg, "PNG", 70, 40, 180, 90);
      doc.text(100, 145, "Kalalajien mritykset", { align: "center" });
      // Table straight from html
      doc.autoTable({
        html: "#fish-weights-table",
        styles: {
          overflow: "linebreak",
          cellWidth: "wrap",
          rowPageBreak: "avoid",
          halign: "justify",
          fontSize: "8",
          lineColor: "100",
          lineWidth: ".25",
        },
        columnStyles: { text: { cellwidth: "auto" } },
        theme: "striped",
        pageBreak: "auto",
        tableWidth: "auto",
        startY: 150,
        margin: { top: 20 },
      });

      doc.text(100, doc.autoTable.previous.finalY + 20, "Yleisi tilastoja", {
        align: "center",
      });
      doc.autoTable({
        html: "#misc-table",
        styles: {
          overflow: "linebreak",
          cellWidth: "wrap",
          rowPageBreak: "avoid",
          halign: "justify",
          fontSize: "8",
          lineColor: "100",
          lineWidth: ".25",
        },
        columnStyles: { text: { cellwidth: "auto" } },
        theme: "striped",
        pageBreak: "auto",
        tableWidth: "auto",
        margin: { top: 20 },
        startY: doc.autoTable.previous.finalY + 25,
      });

      // Save to pdf
      doc.save(
        `${moment(this.competition.start_date).year()}_${this.replaceAll(
          this.competition.name,
          " ",
          ""
        )}_${this.replaceAll(
          this.capitalize_words(competition_type),
          " ",
          ""
        )}.pdf`
      );
    },
    // Saves all the chosen tables to pdf
    saveAllAsPDF: function() {
      let temp_selected_biggest_fish = this.selected_biggest_fish;
      let temp_selected_biggest_amount = this.selected_biggest_amount;
      let temp_selected_normal = this.selected_normal;
      // Format dates for easier reding
      let temp_start_date = this.formatDate(this.competition.start_date);
      let temp_end_date = this.formatDate(this.competition.end_date);
      let year = moment(this.competition.start_date).year();

      let doc = new jsPDF();

      // Title
      const title = `${this.competition.name}`;
      const date =
        temp_start_date === temp_end_date
          ? String(temp_start_date)
          : `${temp_start_date} - ${temp_end_date}`;
      const time = `${date}, Klo. ${this.competition.start_time} - ${this.competition.end_time}`;
      doc.setFontSize(24);
      doc.text(10, 10, title, { align: "left" });
      doc.setFontSize(14);
      doc.text(10, 20, this.competition.cup_name, { align: "left" });
      doc.text(10, 30, time, { align: "left" });
      doc.line(0, 35, 400, 35);
      doc.setFontSize(18);

      // start_coord needed to keep track of y coordinates for tables (if there are no results -> no table drawn to pdf -> varying coordinates)
      let start_coord;
      let rows;
      let columns;
      //Normaalikilpailu (Pisteet), saved to pdf if it's inclued in this.selected_print array
      if (this.selected_print.includes("normal")) {
        // Other tables are generated in code so no need to wait for rendering to html
        columns = [
          "Sijoitus",
          "Nro.",
          "Kapteeni",
          "Varakapteeni",
          "Paikkakunta",
          "Tulos",
          "Sij. pisteet",
          "Osal. pisteet",
          "Yht.",
        ];
        // Format dictionary/json to format that autotable understands (arrays in arrays);
        rows = this.dictToArray(this.normal_points, 1);
        doc.text(100, 50, "Normaalikilpailun tulokset (Pisteet)", {
          align: "center",
        });
        // Table generated in code
        doc.autoTable({
          head: [columns],
          body: rows,
          styles: {
            overflow: "linebreak",
            cellWidth: "wrap",
            rowPageBreak: "avoid",
            halign: "justify",
            fontSize: "8",
            lineColor: "100",
            lineWidth: ".25",
          },
          columnStyles: { text: { cellwidth: "auto" } },
          theme: "striped",
          pageBreak: "auto",
          tableWidth: "auto",
          startY: 55,
          margin: { top: 20 },
        });

        //Normaalikilpailu (Kalat)
        doc.addPage();
        columns = ["Sijoitus", "Nro.", "Kapteeni"];
        // Get fish names for columns
        this.table_fish_names.forEach((name) => {
          columns.push(name);
        });
        columns.push("Tulos");

        rows = this.dictToArray(this.normal_weights, 2);
        doc.text(100, 10, "Normaalikilpailun tulokset (Kalat)", {
          align: "center",
        });
        // Table generated in code
        doc.autoTable({
          head: [columns],
          body: rows,
          styles: {
            overflow: "linebreak",
            cellWidth: "wrap",
            rowPageBreak: "avoid",
            halign: "justify",
            fontSize: "8",
            lineColor: "100",
            lineWidth: ".25",
          },
          columnStyles: { text: { cellwidth: "auto" } },
          theme: "striped",
          pageBreak: "auto",
          tableWidth: "auto",
          margin: { top: 20 },
          startY: 20,
        });
      }

      // Tiimikilpailu, drawn to pdf if it exists and , if it's inclued in this.selected_print array
      if (this.isTeamCompetition && this.selected_print.includes("team")) {
        // If there is "Normaalikilpailun tulokset" selected also, start from new page
        if (this.selected_print.includes("normal")) {
          doc.addPage();
        }
        doc.setFontSize(24);
        doc.text(10, 10, title, { align: "left" });
        doc.setFontSize(14);
        doc.text(10, 20, this.competition.cup_name, { align: "left" });
        doc.text(10, 30, time, { align: "left" });
        doc.line(0, 35, 400, 35);
        doc.setFontSize(18);
        doc.text(100, 50, "Tiimikilpailun tulokset", { align: "center" });
        // Add results, if there are any
        if (this.team_results.length) {
          // Other tables are generated in code so no need to wait for rendering to html
          columns = [
            "Sijoitus",
            "Tiimi",
            "Jsen 1",
            "Jsen 2",
            "Jsen 3",
            "Pisteet",
          ];
          // Format dictionary/json to format that autotable understands (arrays in arrays);
          rows = this.dictToArray(this.normal_points, 1);
          //TODO generate table in code instead of html, like the others
          doc.autoTable({
            head: [columns],
            body: rows,
            styles: {
              overflow: "linebreak",
              cellWidth: "wrap",
              rowPageBreak: "avoid",
              halign: "justify",
              fontSize: "8",
              lineColor: "100",
              lineWidth: ".25",
            },
            columnStyles: { text: { cellwidth: "auto" } },
            theme: "striped",
            pageBreak: "auto",
            tableWidth: "auto",
            margin: { top: 20 },
            startY: 55,
          });
        }
      }

      //"Suurimmat kalat" to pdf if it's inclued in this.selected_print array
      if (this.selected_print.includes("biggest_fishes")) {
        // If there is content before, start from new page
        if (
          this.selected_print.includes("normal") ||
          this.selected_print.includes("team")
        ) {
          doc.addPage();
        }
        let counter = 0;
        // For each fish, generate tables for "Suurimmat Kalat (Kala)" and "Suurimmat Kalasaaliit (Kala)"
        this.table_fish_names.forEach((name) => {
          // Same process as above, but for every fish instead of only winners
          this.selected_biggest_fish = name;
          this.calculateBiggestFishes();
          start_coord = 10;

          if (this.biggest_fishes_results.length) {
            // So it doesn't add unnecessary page on the first loop
            if (counter > 0) {
              doc.addPage();
            }
            doc.setFontSize(24);
            doc.text(10, 10, title, { align: "left" });
            doc.setFontSize(14);
            doc.text(10, 20, this.competition.cup_name, { align: "left" });
            doc.text(10, 30, time, { align: "left" });
            doc.line(0, 35, 400, 35);
            doc.setFontSize(18);
            start_coord = 50;

            columns = ["Sijoitus", "Veneen nro", "Kapteeni", "Paino"];
            rows = this.dictToArray(this.biggest_fishes_results, 3);

            doc.text(100, start_coord, "Suurimmat kalat" + ` (${name})`, {
              align: "center",
            });

            doc.autoTable({
              head: [columns],
              body: rows,
              styles: {
                overflow: "linebreak",
                cellWidth: "wrap",
                rowPageBreak: "avoid",
                halign: "justify",
                fontSize: "8",
                lineColor: "100",
                lineWidth: ".25",
              },
              columnStyles: { text: { cellwidth: "auto" } },
              theme: "striped",
              pageBreak: "auto",
              tableWidth: "auto",
              margin: { top: 20 },
              startY: start_coord + 5,
            });
            counter++;
          }
        });
      }

      // Suurimmat kalasaaliit to pdf if it's inclued in this.selected_print array
      if (this.selected_print.includes("biggest_amounts")) {
        // If there is content before, start from new page
        if (
          this.selected_print.includes("normal") ||
          this.selected_print.includes("team") ||
          this.selected_print.includes("biggest_fishes")
        ) {
          doc.addPage();
        }
        let counter = 0;
        this.table_fish_names.forEach((name) => {
          this.selected_biggest_amount = name;
          this.calculateBiggestAmounts();
          start_coord = 10;

          if (this.biggest_amounts[name].length) {
            // So it doesn't add unnecessary page on the first loop
            if (counter > 0) {
              doc.addPage();
            }
            doc.setFontSize(24);
            doc.text(10, 10, title, { align: "left" });
            doc.setFontSize(14);
            doc.text(10, 20, this.competition.cup_name, { align: "left" });
            doc.text(10, 30, time, { align: "left" });
            doc.line(0, 35, 400, 35);
            doc.setFontSize(18);
            start_coord = 50;

            rows = this.dictToArray(this.biggest_amounts[name], 3);
            doc.text(100, start_coord, "Suurimmat kalasaaliit" + ` (${name})`, {
              align: "center",
            });

            doc.autoTable({
              head: [columns],
              body: rows,
              styles: {
                overflow: "linebreak",
                cellWidth: "wrap",
                rowPageBreak: "avoid",
                halign: "justify",
                fontSize: "8",
                lineColor: "100",
                lineWidth: ".25",
              },
              columnStyles: { text: { cellwidth: "auto" } },
              theme: "striped",
              pageBreak: "auto",
              tableWidth: "auto",
              startY: start_coord + 5,
            });
            counter++;
          }
        });
      }

      // "Suurimmat Kalat (Voittajat) / Suurimmat kalasaaliit (Voittajat)"" to pdf if it's inclued in this.selected_print array
      if (this.selected_print.includes("biggest_winners")) {
        // If there is content before, start from new page
        if (
          this.selected_print.includes("normal") ||
          this.selected_print.includes("team") ||
          this.selected_print.includes("biggest_fishes") ||
          this.selected_print.includes("biggest_amounts")
        ) {
          doc.addPage();
        }
        // Suurimmat Kalat  (Voittajat)
        // Select these for calculations
        this.selected_biggest_fish = this.selected_biggest_amount = "Voittajat";
        columns = ["Kalalaji", "Veneen nro", "Kapteeni", "Paino"];
        // Calculate data
        this.calculateBiggestFishes();
        this.calculateBiggestAmounts();
        // If there are any results, add title
        if (
          this.biggest_fishes_results.length ||
          this.biggest_amounts_results.length
        ) {
          doc.setFontSize(24);
          doc.text(10, 10, title, { align: "left" });
          doc.setFontSize(14);
          doc.text(10, 20, this.competition.cup_name, { align: "left" });
          doc.text(10, 30, time, { align: "left" });
          doc.line(0, 35, 400, 35);
          doc.setFontSize(18);
        }

        // If there are biggest fishes
        if (this.biggest_fishes_results.length) {
          rows = this.dictToArray(this.biggest_fishes_results, 4);
          doc.text(
            100,
            50,
            "Suurimmat kalat" + ` (${this.selected_biggest_fish})`,
            { align: "center" }
          );
          // Table generated in code
          doc.autoTable({
            head: [columns],
            body: rows,
            styles: {
              overflow: "linebreak",
              cellWidth: "wrap",
              rowPageBreak: "avoid",
              halign: "justify",
              fontSize: "8",
              lineColor: "100",
              lineWidth: ".25",
            },
            columnStyles: { text: { cellwidth: "auto" } },
            theme: "striped",
            pageBreak: "auto",
            tableWidth: "auto",
            margin: { top: 20 },
            startY: 55,
          });
          // Keep track of y coordinate
          start_coord = doc.autoTable.previous.finalY + 25;
        } else {
          // If no biggest fishes, biggest amounts table starts from 50 instead
          start_coord = 50;
        }

        //Suurimmat kalasaaliit (Voittajat)
        // If there are any amounts --> if someone has gotten any fish
        if (this.biggest_amounts_results.length) {
          rows = this.dictToArray(this.biggest_amounts_results, 4);
          doc.text(
            100,
            start_coord,
            "Suurimmat kalasaaliit" + ` (${this.selected_biggest_fish})`,
            { align: "center" }
          );
          // Table generated in code
          doc.autoTable({
            head: [columns],
            body: rows,
            styles: {
              overflow: "linebreak",
              cellWidth: "wrap",
              rowPageBreak: "avoid",
              halign: "justify",
              fontSize: "8",
              lineColor: "100",
              lineWidth: ".25",
            },
            columnStyles: { text: { cellwidth: "auto" } },
            theme: "striped",
            pageBreak: "auto",
            tableWidth: "auto",
            margin: { top: 20 },
            startY: start_coord + 5,
          });
        }
      }
      // "Tilastoja" to pdf if it's inclued in this.selected_print array
      if (this.selected_print.includes("stats")) {
        // If there is content before, start from new page
        if (
          this.selected_print.includes("normal") ||
          this.selected_print.includes("team") ||
          this.selected_print.includes("biggest_fishes") ||
          this.selected_print.includes("biggest_amounts") ||
          this.selected_print.includes("biggest_winners")
        ) {
          doc.addPage();
        }
        doc.setFontSize(24);
        doc.text(10, 10, title, { align: "left" });
        doc.setFontSize(14);
        doc.text(10, 20, this.competition.cup_name, { align: "left" });
        doc.text(10, 30, time, { align: "left" });
        doc.line(0, 35, 400, 35);
        doc.setFontSize(18);
        // "Tilastot"
        var fishesImg = document
          .getElementById("fishesChart")
          .toDataURL("image/png", 1.0);
        var signeeImg = document
          .getElementById("signeesChart")
          .toDataURL("image/png", 1.0);
        doc.addImage(fishesImg, "PNG", -30, 40, 180, 90);
        doc.addImage(signeeImg, "PNG", 70, 40, 180, 90);
        doc.text(100, 165, "Kalalajien mritykset", { align: "center" });
        // Table generated straight from html
        doc.autoTable({
          html: "#fish-weights-table",
          styles: {
            overflow: "linebreak",
            cellWidth: "wrap",
            rowPageBreak: "avoid",
            halign: "justify",
            fontSize: "8",
            lineColor: "100",
            lineWidth: ".25",
          },
          columnStyles: { text: { cellwidth: "auto" } },
          theme: "striped",
          pageBreak: "auto",
          tableWidth: "auto",
          startY: 170,
          margin: { top: 20 },
        });

        doc.text(100, doc.autoTable.previous.finalY + 20, "Yleisi tilastoja", {
          align: "center",
        });
        // Table generated straight from html
        doc.autoTable({
          html: "#misc-table",
          styles: {
            overflow: "linebreak",
            cellWidth: "wrap",
            rowPageBreak: "avoid",
            halign: "justify",
            fontSize: "8",
            lineColor: "100",
            lineWidth: ".25",
          },
          columnStyles: { text: { cellwidth: "auto" } },
          theme: "striped",
          pageBreak: "auto",
          tableWidth: "auto",
          margin: { top: 20 },
          startY: doc.autoTable.previous.finalY + 25,
        });
      }

      // Reset variables
      this.selected_biggest_fish = temp_selected_biggest_fish;
      this.selected_biggest_amount = temp_selected_biggest_amount;
      this.selected_normal = temp_selected_normal;
      this.calculateBiggestFishes();
      this.calculateBiggestAmounts();

      // Save to pdf
      doc.save(
        `${year}_${this.replaceAll(this.competition.name, " ", "")}Tulokset.pdf`
      );
    },
  },
};

// Custom range function for for loop, with recursion which is more efficient
function* range(start, end) {
  yield start;
  if (start === end) return;
  yield* range(start + 1, end);
}
</script>
